import React from 'react';
import { Loader } from '../components';
import { HouseCard, NewHouseCard } from './_Card';
import { Grid } from '@material-ui/core';

export default function HousesList(props) {
  const { houses } = props
  
  const Houses = () => houses.map(h => (
    <Grid item key={h.slug}>
      {/* <CurrencyFormat
        value={h.price}
        displayType="text"
        thousandSeparator prefix="$"
        renderText={value => <Typography className="currency-format" variant="h6">{value}</Typography>}
      />
      {h.name} */}
      <HouseCard house={h} />
    </Grid>
  ));

  const List = () => houses.length === 0? 
    <Loader message="Cargando casas..." absolute/>: 
    <Grid
      container
      direction="row"
      justify="center"
      alignItems="center"
      spacing={4}
    >
      <Houses/>
      <Grid><NewHouseCard/></Grid>
    </Grid>

  return <List/>
}
